import React, { Component } from "react";
import {
  Row,
  Card,
  CardTitle,
  Label,
  // Input,
  FormGroup,
  Button
} from "reactstrap";
import { Formik, Form, Field } from "formik";

import { NavLink } from "react-router-dom";
import { connect } from "react-redux";
import { registerUser, verifyPhoneNumber } from "../../redux/actions";

import IntlMessages from "../../helpers/IntlMessages";
import { Colxx } from "../../components/common/CustomBootstrap";
import MaskedInput from "react-text-mask";

class Register extends Component {
  constructor(props) {
    super(props);

    this.state = {
      phone_number: "",
      code: "",
      code_waiter_counter: 120
    };
  }

  onUserRegister = values => {
    if (!this.props.loading) {
      if (values.code !== "") {
        this.props.registerUser(values, this.props.history);
      } else if (values.phone_number !== "") {
        const { phone_number } = values;
        values.phone_number = `+${phone_number.replace(/\D/g, "")}`;

        this.requestAuthCode(values.phone_number, this.props.history);
      }
    }
  };

  validatePhoneNumber = value => {
    let error;
    if (!value) {
      error = "Please enter your phone number";
    } else if (!/\+\d+/.test(value) || value.length < 13) {
      error = "Invalid phone number";
    }

    return error;
  };

  validateCode = value => {
    let error;
    if (!value) {
      error = "Please enter your code";
    } else if (!/\d{4}/.test(value)) {
      error = "Code must be 4 characters";
    }

    return error;
  };

  codeEnterIntervalId;
  runCodeReenterWaiter() {
    this.codeEnterIntervalId = window.setInterval(() => {
      const { code_waiter_counter } = this.state;

      if (this.codeEnterIntervalId && code_waiter_counter === 0) {
        window.clearInterval(this.codeEnterIntervalId);
      } else {
        this.setState({
          code_waiter_counter: code_waiter_counter - 1
        });
      }
    }, 1000);
  }

  requestAuthCode(phone_number, history, $event) {
    if ($event) {
      $event.preventDefault();
    }

    if (phone_number !== "") {
      this.props.verifyPhoneNumber(phone_number, history);

      this.runCodeReenterWaiter();
    }
  }

  componentWillUnmount() {
    window.clearInterval(this.codeEnterIntervalId);
  }

  render() {
    const { code, phone_number, code_waiter_counter } = this.state;
    const initialValues = { phone_number, code, code_waiter_counter };
    const phoneNumberMask = [
      "+",
      /\d/,
      /\d/,
      /\d/,
      " ",
      "(",
      /\d/,
      /\d/,
      ")",
      " ",
      /\d/,
      /\d/,
      /\d/,
      " ",
      /\d/,
      /\d/,
      " ",
      /\d/,
      /\d/
    ];

    return (
      <Row className="h-100">
        <Colxx
          xxs="12"
          xs="12"
          sm="12"
          md="10"
          lg="8"
          xl="8"
          xxl="6"
          className="mx-auto my-auto"
        >
          <Card className="auth-card">
            {/* <div className="position-relative image-side ">
              <p className="text-white h2">MAGIC IS IN THE DETAILS</p>
              <p className="white mb-0">
                To create a caregiver or doctor account, please type in your
                phone number. <br />
                If you are a member, please{" "}
                <NavLink to={`/user/login`} className="white">
                  login
                </NavLink>
                .
              </p>
            </div> */}
            <div className="form-side">
              <NavLink to={`/`} className="white">
                <span className="logo-single" />
              </NavLink>
              <CardTitle className="mb-4">
                <IntlMessages id="user.register" />
              </CardTitle>
              <Formik
                initialValues={initialValues}
                onSubmit={this.onUserRegister}
              >
                {({ errors, touched, handleChange, handleBlur }) => (
                  <Form className="av-tooltip tooltip-label-bottom">
                    <FormGroup className="form-group has-float-label">
                      <Label>
                        <IntlMessages id="user.phone-number" />
                      </Label>
                      <Field
                        name="phone_number"
                        validate={this.validatePhoneNumber}
                      >
                        {({ field }) => (
                          <MaskedInput
                            {...field}
                            className="form-control"
                            id="phone_number"
                            placeholder="+000 (00) 000 00 00"
                            type="text"
                            mask={phoneNumberMask}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            disabled={this.props.activeVerificationCode}
                          />
                        )}
                      </Field>

                      {errors.phone_number && touched.phone_number && (
                        <div className="invalid-feedback d-block">
                          {errors.phone_number}
                        </div>
                      )}
                    </FormGroup>

                    {this.props.activeVerificationCode && (
                      <FormGroup className="form-group has-float-label">
                        <Label>
                          <IntlMessages id="user.code" />
                        </Label>
                        <Field
                          className="form-control"
                          type="text"
                          name="code"
                          validate={this.validateCode}
                        />
                        {errors.code && touched.code && (
                          <div className="invalid-feedback d-block">
                            {errors.code}
                          </div>
                        )}
                      </FormGroup>
                    )}

                    <div className="d-flex justify-content-between align-items-center">
                      <a
                        href="/"
                        onClick={$event =>
                          this.requestAuthCode(
                            this.state.phone_number,
                            this.props.history,
                            $event
                          )
                        }
                        className={`btn p-0 ${
                          code_waiter_counter === 0 ? "" : "disabled"
                        }`}
                      >
                        <IntlMessages id="user.not-receive-code" />
                      </a>

                      <NavLink to={`/user/login`}>
                        <IntlMessages id="user.login-title" />
                      </NavLink>

                      <Button
                        color="primary"
                        className={`btn-shadow btn-multiple-state ${
                          this.props.loading ? "show-spinner" : ""
                        }`}
                        size="lg"
                        type="submit"
                        disabled={
                          this.props.loading ||
                          (this.props.activeVerificationCode &&
                            errors.code &&
                            touched.code)
                        }
                      >
                        <span className="spinner d-inline-block">
                          <span className="bounce1" />
                          <span className="bounce2" />
                          <span className="bounce3" />
                        </span>
                        <span className="label">
                          {!this.props.activeVerificationCode ? (
                            <IntlMessages id="user.verify" />
                          ) : (
                            <IntlMessages id="user.create-profile" />
                          )}
                        </span>
                      </Button>
                    </div>
                  </Form>
                )}
              </Formik>
            </div>
          </Card>
        </Colxx>
      </Row>
    );
  }
}

const mapStateToProps = ({ authUser }) => {
  const { user, loading, error, activeVerificationCode } = authUser;

  return { user, loading, error, activeVerificationCode };
};

export default connect(mapStateToProps, {
  registerUser,
  verifyPhoneNumber
})(Register);
